@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
.nav{padding:20px;background-color:#a5a5a5}.nav__list{display:flex}.nav__item{margin-right:14px}.nav__link{padding:10px;font-weight:bold;color:#fff;transition:color 0.3s}.nav__link:hover{color:#000}

.PeopleTable{width:1000px;margin:0 auto;border-collapse:collapse}.PeopleTable__thead{font-weight:bold;background-color:#e5e5e5}.PeopleTable td{padding:5px 10px;border:1px solid}

.App{max-width:1400px;margin:0 auto}html{font-family:"Roboto", sans-serif}body{margin:0}ul{margin:0;padding:0;list-style-type:none}a{text-decoration:none}

